/**
 * Colors
 */

$nethinks: #b71717; //FABC04 - 6FB31A 
$theme-colors: (
  "primary": $nethinks,
  "nethinks": $nethinks
);
$white: #FFFFFF;

/**
 * Fonts & Typo
 */
$font-family-sans-serif: 'Fira Sans', "Helvetica Neue", Arial, sans-serif;

/**
 * Breadcrumb
 */
$breadcrumb-border-radius: 0px;
$breadcrumb-bg: darken($white, 5%);

/**
 * Alerts
 */
$alert-border-width: 2px;
$alert-border-radius: 0px;
