nav {
  height: $navbar-height;
  background:  $navbar-bg-color;
  // border-bottom: $navbar-border-width $navbar-border-style theme-color("primary") ;
  .navbar-brand{
    width: $navbar-brand-width;
    font-weight: $navbar-brand-font-width;
    b{
      font-weight: $navbar-brand-font-width-bold;
      padding-right: $navbar-brand-small-font-right;
    }
    small{
      font-weight: $navbar-brand-small-font-width;
      color: theme-color('primary');
      position: relative;
      top: $navbar-brand-small-font-top;
      margin-right: $navbar-brand-small-font-right;
    }
  }
  .navbar-nav{
    li{
      a{
        color: $navbar-icon-link;
      }
    }
  }
  #nav-quick-search{
    >input{
      padding: $navbar-search-padding;
      border-radius: $navbar-search-border;
    }
  }
}
